.header{
    position: sticky;
    top: 0px;
    height: 7vh;
    width: 100%;
    background-color: hsla(231, 48%, 48%, 1);
    color: hsla(0, 0%, 100%, 1);
    box-shadow: 0 0 0.2rem rgb(0 0 0 / 10%), 0 0.2rem 0.4rem rgb(0 0 0 / 20%);
}

.header-container{
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    align-items:center;
    justify-content: space-between;
}

.header-title-pc{
    display: flex;
    align-items:center;
}

.header-title-cel{
    display: none;
}

.header-logo-container{
    margin: .2rem;
    padding: .4rem;
}

.header-logo{
    width: 1.6rem;
    height: 1.6rem;
    fill: hsla(0, 0%, 100%, 1);
}

.header-title-container{
    padding: 0 1rem;
    font-size: 1.2rem;
}

.log-button-container{
    margin-right: 5.5rem;
}

.log-button{
    font-size: 14px !important;
    text-transform: capitalize !important;
}

.footer{
    position: fixed;
    width: 100%;
    height: 6vh;
    background-color: hsla(231, 48%, 48%, 1);
    color: hsla(0, 0%, 100%, 1);
    font-family: "Roboto",-apple-system,BlinkMacSystemFont,Helvetica,Arial,sans-serif;
}

.footer-container{
    max-width: 75rem;
    margin: auto;
    display: flex;
    justify-content: space-between;
    
}

.prev-section{
    text-align: right;
    
}

.next-section{
    text-align: left;
}

.visible{
    display: block;
}

.not-visible{
    display: none;
}

.header-title-cel-sub{
    display: flex;
    margin: auto;
    align-items: center;

}

.header-space{
    display: flex;
    align-items:center;
    justify-content: space-between;
    margin-right: 3.5rem;
}

.logo-dyslexia{
    height: 40px;
}

.flex{
    display: flex;
}

.align-center{
    align-items: center;
}

.justify-end{
    justify-content: end !important;
}

.bulb-icon{
    color: black;
    font-size:x-large;
}

.MuiList-root{
    background-color: #f2f2f2 !important;
}

@media only screen and (max-width: 1200px){

    .header-title-pc{
        display: none;
    }
    .header-title-cel{
        display: flex;
        align-items:center;
        justify-content: space-between;
    }

    .header-logo-container{
        margin: 0;
        margin-right: .2rem;
        padding: 0;
    }

    .header-title-container{
        padding: 0;
        font-size: 0.75rem;
        text-align: left;
    }

    .log-button-container{
        margin-right: 0;
    }

    .log-button{
        font-size: 12px !important;
        text-transform: capitalize !important;
    }
}